let html = document.querySelector("#html");//ÈÄöËøácssÈÄâÊã©Âô®ÊâæÂà∞Ëøô‰∏™ÂÖÉÁ¥†
let style = document.querySelector("#style");

let string = `/* ‰Ω†Â•ΩÔºåÊàëÂè´Fish
 * Êé•‰∏ãÊù•ÊàëÊºîÁ§∫‰∏Ä‰∏ãÊàëÁöÑÂâçÁ´ØÂäüÂ∫ï
 * È¶ñÂÖàÊàëË¶ÅÂáÜÂ§á‰∏Ä‰∏™div
 **/
#div1{
    border: 1px solid red;
    width: 200px;
    height: 200px;
}
/* Êé•‰∏ãÊù•ÊàëÊää div ÂèòÊàê‰∏Ä‰∏™ÂÖ´Âç¶Âõæ
 * Ê≥®ÊÑèÁúãÂ•Ω‰∫Ü
 * È¶ñÂÖàÔºåÊää div ÂèòÊàê‰∏Ä‰∏™ÂúÜ
 **/
#div1{
    border-radius: 50%;
    box-shadow: 0 0 3px rgba(0,0,0,0.5);
    border: none;
}
/* ÂÖ´Âç¶ÊòØÈò¥Èò≥ÂΩ¢ÊàêÁöÑ
 * ‰∏ÄÈªë‰∏ÄÁôΩ
 **/
#div1{
    background: linear-gradient(90deg, rgba(255,255,255,1) 0%, rgba(255,255,255,1) 50%, rgba(0,0,0,1) 50%, rgba(0,0,0,1) 100%);
}
/* Âä†‰∏§‰∏™Á•ûÁßòÁöÑÂ∞èÁêÉ */
#div1::before{
    width: 100px;
    height: 100px;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    background: #000;
    border-radius: 50%;
    background: radial-gradient(circle, rgba(255,255,255,1) 0%, rgba(255,255,255,1) 25%, rgba(0,0,0,1) 25%, rgba(0,0,0,1) 100%);
}
#div1::after{
    width: 100px;
    height: 100px;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    background: #fff;
    border-radius: 50%;
    background: radial-gradient(circle, rgba(0,0,0,1) 0%, rgba(0,0,0,1) 25%, rgba(255,255,255,1) 25%, rgba(255,255,255,1) 100%, rgba(0,0,0,1) 100%);
}
`;
let string2 = "";
let n = 0;
console.log(string.length);//ËøòÂèØ‰ª•Âà©Áî®Ë∞ÉËØïÂ§ßÊ≥ïÔºåstring[0].charCodeAt(),Ëé∑ÂèñUnicodeÂÄº

let step = () => {
  setTimeout(() => {
    // Â¶ÇÊûúÊòØÂõûËΩ¶ÔºåÂ∞±‰∏çÁÖßÊê¨
    if (string[n] === "\n") {
      string2 += "<br>";
    } 
        // Â¶ÇÊûú‰∏çÊòØÂõûËΩ¶Â∞±ÁÖßÊê¨
    else if (string[n] === " ") {
      string2 += "&nbsp;";
    } 
    else {
      string2 += string[n];
    }
    html.innerHTML = string2;
    style.innerHTML = string.substring(0, n);
    window.scrollTo(0, 99999);//È°µÈù¢ÊªöÂä®
    html.scrollTo(0, 99999);
    if (n < string.length - 1) {
      // Â¶ÇÊûú n ‰∏çÊòØÊúÄÂêé‰∏Ä‰∏™,Â∞±ÁªßÁª≠
      n += 1;
      step();
    }
  }, 100);
};

step(); // 1=>2